import Menu from './Menu'
import {FeatureType} from '../model/feature/FeatureType'

export default class MenuManager {

}

//
MenuManager.refreshMenus = function (user) {

  let menus = []

  if (user.role === 'GUEST') {

    //
    let loginMenu = new Menu('', '/user/login', false, 'fa fa-user-circle-o')
    menus.push(loginMenu)

  } else {

    //
    let matterListMenu = new Menu('', '/', false, 'fa fa-th')
    menus.push(matterListMenu)

    if(user.role === 'ADMINISTRATOR'){
      //
      let preferenceMenu = new Menu('', '/preference', false, 'fa fa-dashboard')
      menus.push(preferenceMenu)

      //
      let userMenu = new Menu('', '/user/list', false, 'fa fa-user')
      menus.push(userMenu)
    }


    //
    let logoutMenu = new Menu('', '/user/login', false, 'fa fa-power-off')
    menus.push(logoutMenu)

  }

  return menus
}




